import { Box } from "@chakra-ui/react";
import '../Inspirations.css';

const CeramicPorcelain = () => {
    return (
        <Box maxWidth="8xl" margin="30px 60px" display="flex" flexDirection="column" gap="1.2rem">
            <h3 style={{ fontSize: "22px" }}><strong>Ceramic vs Porcelain – Which One Should I Choose for My Malaysian Home?</strong></h3>
            <hr></hr>
            <img src="https://lorenzaceramica.com/wp-content/uploads/2020/11/lorenza-ceramica-ceramic-vs-porcelain-malaysia-1024x278.jpg" alt="post-banner"></img>
            <p>The most common tiles that you’ll find in modern Malaysian homes are ceramic and porcelain.</p>
            <p>They are common for a reason. Fancy tiles such as granite are pricier, so they are reserved for specific areas such as kitchen countertops. Meanwhile, traditional materials such as wood are being replaced with alternatives that can mimic the same look, but without all the drawbacks.</p>
            <p>What are the differences between ceramic and porcelain? They look the same, and some people use the terms interchangeably. So why is one more expensive than the other?</p>
            <p>In this brief guide, we will elaborate on their different characteristics so you can make an informed decision.</p>
            <h2 className="has-text-color">What are the differences between ceramic and porcelain?</h2>
            <p>By category, ceramic and porcelain fall into the same umbrella – the ceramic family.</p>
            <p>What distinguishes them when it comes to grading is their water absorption rate.</p>
            <p>Porcelain tiles have a water absorption rate of 0.5% or lower, while ceramic tiles absorb more. This is due to their material composition and manufacturing process, which, in return, affects other attributes including:</p>
            <table className="has-subtle-pale-blue-background-color has-fixed-layout has-background"><thead><tr><th></th><th>Ceramic Tile</th><th>Porcelain Tile</th></tr></thead><tbody><tr><td>Construction</td><td>Made with coarser clay and fired at a lower temperature</td><td>Made with denser clay and fired at a higher temperature</td></tr><tr><td>Water absorption rate</td><td>More than&nbsp;6%</td><td>Less than 0.5%</td></tr><tr><td>Resistance to moisture</td><td>Good</td><td>Excellent</td></tr><tr><td>Resistance to stain</td><td>Good</td><td>Excellent</td></tr><tr><td>Durability</td><td>Prone to cracking and breaking</td><td>Highly durable</td></tr><tr><td>Design</td><td>Limited</td><td>Versatile</td></tr><tr><td>Installation</td><td>Easy to cut and install, DIY-friendly</td><td>More brittle and may need professional services</td></tr></tbody></table>
            <h2 className="has-text-color">Which tiles should I use for my house?</h2>
            <p>Ask any tile supplier and they will recommend porcelain tiles right away due to their high quality. Now, being Malaysian, we also want to score the best deal and save as much money as we can!</p>
            <p>Thus, remember to take into account all considerations, including:</p>
            <h2 className="has-text-color">Appearance</h2>
            <p>Basic ceramic and porcelain look the same, both with solid colours and patterns.</p>
            <p>Yet, recent technology has enabled porcelain to mimic the look of other materials. This allows for more aesthetic choices such as a vintage wooden feel or a lavish granite look.</p>
            <h2 className="has-text-color">Durability</h2>
            <p>Generally, porcelain tiles are more durable than ceramic tiles.</p>
            <p>Porcelain tiles are harder, denser, less porous, and more impervious to moisture. They are suitable for outdoor use as well as in damp areas such as the kitchen and the bathroom.</p>
            <p>Ceramic tiles are more suitable for indoor use. If you want to use ceramic for the kitchen or bathroom, make sure the tiles are glazed to minimise water infiltration.</p>
            <h2 className="has-text-color">Cost</h2>
            <p>Porcelain tiles are more expensive to buy, and they can also be trickier to install. While ceramic tiles are easier to cut and install by DIY enthusiasts, porcelain tiles can be brittle and better left to the hands of professionals.</p>
            <h2><a href="https://lorenzaceramica.com/products/types/wall-tiles" target="_blank" rel="noreferrer noopener">Are you looking for tile suppliers in Malaysia? Click here to check out the best price for floor and wall tiling in the Klang Valley!</a></h2>
        </Box>
    )
}

export default CeramicPorcelain;